@import url(https://fonts.googleapis.com/css?family=Josefin+Sans:400,600,700&display=swap);
.banner {
    width: 100%;
    height: 40vh;
    max-height: 40vh;
    overflow: hidden;
}
.banner-img {
    width: 100%;
}
@media only screen and (min-width: 1100px) {
    .banner-img {
        -webkit-transform: translateY(-200px);
                transform: translateY(-200px);
    }
}
@media only screen and (max-width: 1100px) {
    .banner {
        width: 100%;
        height: auto;
        overflow: hidden;
    }
    .banner-img {
        -webkit-transform: translateY(-100px);
                transform: translateY(-100px);
    }
}
@media only screen and (max-width: 700px) {
    .banner {
        width: 100%;
        height: auto;
        overflow: hidden;
    }
    .banner-img {
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
    }
}

.recipe-container {
    margin: 0 auto;
    width: 100%;
}
.recipes-heading {
    text-align: center;
}
.recipes-heading h2 {
    font-size: 3rem;
    padding: 1rem 0.5rem;
    color: #ffffff;
    background: #ffffff;
    text-shadow: 1px 3px 0 #969696, 1px 13px 5px #aba8a8;
}
.recipe {
    width: 80%;
    margin: 0 auto;
    padding: 1rem;
}
.recipe-img {
    width: 100%;
}
.recipe-name {
    color: #ffffff;
    text-shadow: 1px 3px 0 #969696, 1px 13px 5px #aba8a8;
}
.recipe-name-container {
    background-image: url(/honey/static/media/background.23716469.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    text-align: center;
}

body {
    min-width: 320px;
}
.dark-orange {
    color: #fca336;
}

.honey-color {
    color: #fcb836;
}
.pointer {
    cursor: pointer;
}
/* Heading */
.heading-1 {
    color: #fcb836;
}
.heading-2 {
    color: #fcb836;
}

.text-block {
    font-size: 10px;
}
.text-block > footer {
    font-size: 2em;
}
.text-block footer ul {
    font-size: 2em;
}
.text-block footer ul li {
    font-size: 2em;
}
.footer-logo-container {
    width: 100%;
}

/* BEES */
.bees {
    padding: 1rem;
    width: 95%;
    margin: 0 auto;
}
/* RECIPES */
.recipes-container {
    display: flex;
    justify-content: center;
}
@media (max-width: 810px) {
    .recipes-container {
        display: flex;
        flex-direction: column;
    }
}
@media (max-width: 360px) {
    .recipes-container {
        display: flex;
        flex-direction: column;
    }
}

body {
    height: 100%;
    width: 100%;
    font-family: "Josefin Sans", sans-serif;
    color: grey;
}

.flex {
    display: flex;
}

.navbar-brand img {
    width: 110px;
}

.nav-item {
    padding-right: 20px;
}

.navbar-nav li {
    font-size: 1.1rem !important;
}

.carousel-inner {
    height: 600px;
}
.carousel-item img {
    width: 100%;
    height: auto;
}

/* Text P */

.carousel-caption {
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-80%);
            transform: translateY(-80%);
    text-shadow: 1px 1px 10px black;
}

/* .carousel-indicators{
  position: absolute;
  top: 75%;
} */

@media (max-width: 810px) {
    .carousel-caption .display-2 {
        font-size: 3em;
    }

    .carousel-inner {
        height: auto;
    }
    .lead {
        font-size: 3vw;
    }
    .navbar-brand img {
        width: 140px;
    }
}

@media (max-width: 360px) {
    .carousel-caption {
        position: absolute;
        top: 40%;
    }
    .carousel-caption .display-2 h3 {
        font-size: 10px;
    }
    .btn {
        font-size: 7px;
    }
    .navbar-brand img {
        width: 140px;
    }
}

.jumbotron {
    padding: 1em;
    border-radius: 0;
    width: 70%;
    padding: 20px;
    margin: 0 auto;
}

.padding {
    padding-bottom: 2em;
}

.alert {
    width: 75%;
    margin: 0 auto;
    padding-top: 2em;
}
.alert hr {
    border-top: 2px solid black;
    width: 95%;
    margin-top: 0.3em;
    margin-bottom: 1em;
}

.fa-html5 {
    color: orange;
}
.fa-bold {
    color: #9900ff;
}
.fa-css3 {
    color: #1a75ff;
}
.fa-react {
    color: blue;
}
.fa-js {
    color: yellow;
}

.fa-html5,
.fa-bold,
.fa-css3,
.fa-react,
.fa-js {
    font-size: 4em;
    margin: 1em;
}

.img-fluid {
    border-radius: 20%;
}

#fixed {
    height: 400px;
    border-top: 4px solid #f0f0f0;
    border-bottom: 4px solid #f0f0f0;
    background: url(/honey/static/media/honeyBg.faf4102d.jpg) no-repeat center center fixed;
    background-blend-mode: multiply;
    background-color: lightblue;
    background-size: cover;
    -o-background-size: cover;
    -moz-background-size: cover;
    -webkit-background-size: cover;
}

#gifsBtn {
    margin-bottom: 20px;
}
#gifs img {
    width: 100%;
    height: 200px;
    margin-bottom: 20px;
}

/* Footer */
.bg-color {
    background-color: #3f2414;
}
@media (max-width: 400px) {
    .footer-logo {
        width: 95%;
    }
}

/* About Honey */
.small-img {
    border-radius: 20px;
    width: 100%;
    max-height: auto;
}
.about-container {
    padding: 2rem;
}
.about-container div {
    width: 90%;
    margin: 0 auto;
}
.flex .left-div {
    width: 70%;
}
.flex .right-div {
    width: 30%;
}
@media (max-width: 810px) {
    .flex .left-div {
        width: 50%;
    }
    .flex .right-div {
        width: 50%;
    }
}
@media (max-width: 500px) {
    .flex {
        display: flex;
        flex-direction: column;
    }
    .flex .left-div {
        width: 100%;
    }
    .flex .right-div {
        width: 100%;
    }
}

